import { createRouter, createWebHistory } from 'vue-router';
import MainPage from './pages/MainPage.vue';
import ExamStart from './pages/ExamStart.vue';
import ExamSteps from './pages/ExamSteps.vue';
import store from './store/index.js';

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { path: process.env.VUE_APP_BaseURL, redirect: '/queez' },
    { path: '/queez', component: MainPage },
    { path: '/startqueez/:QuizTitle', component: ExamStart, props: true},
    { path: '/examsteps/:QuizTitle', component: ExamSteps, props: true}
    /*
    { path: '/admin', component: ShopAdmin, meta: { requiresAuth: true }},
    */
  ],
});

router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth && !store.state.isLoggedIn) {
    next({ path: '/' });
  } else {
    next();
  }
});

export default router;
